    padding: 0;
    margin: 0;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}


.app-bar {
    padding: 8px 16px;
    background-color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
    display: flex;
    align-items: center;
}

.app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 8px;
    cursor: pointer;
}

.app-bar .app-bar__brand {
    display: flex;
    align-items: center;
}

.app-bar .app-bar__brand a img {
    object-fit: contain;
    width: 100px;
}

.app-bar .app-bar__navigation {
    position: absolute;
    top: 50px;
    left: -180px;
    width: 150px;
    transition: all 0.3s;
    padding: 8px;
    background-color: white;
    overflow: hidden;
}

.app-bar .app-bar__navigation.open {
    left: 0;
}

.app-bar .app-bar__navigation ul li a {
    display: inline-block;
    text-decoration: none;
    color: black;
    padding: 8px;
    margin-bottom: 5px;
    width: 100%;
}

main {
    padding: 32px;
    flex: 1;
}

.content {
    margin: 0 auto;
    min-height: 100%;
}

.content .content__heading {
    font-weight: normal;
}


.like {
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #db0000;

    position: fixed;
    bottom: 16px;
    right: 16px;
    border-radius: 50%;
    border: 0;

    font-size: 18px;
    color: white;
    cursor: pointer;
}

footer {
    padding: 20px;
    color: black;
    background-color: #b3c8cf;
    text-align: center;
    font-weight: bold;
}

.skip-link {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #bf1722;
    color: white;
    padding: 8px;
    z-index: 100;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    pointer-events: none;
}

.skip-link:focus {
    opacity: 1;
}

@media screen and (prefers-reduced-motion: no-preference) {

    .skip-link:focus,
    .skip-link:focus-within {
        opacity: 1;
        pointer-events: auto;
    }
}


.restoran-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
}

.restaurant-card {
    padding: 16px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 8px;
}

.restaurant-card img {
    width: 100%;
    border-radius: 8px;
}

.restaurant-card h3 {
    margin-top: 12px;
}

.restaurant-card p {
    margin-top: 8px;
    color: #666;
}

.restaurant-detail {
    margin-bottom: 20px;
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.restaurant-detail__header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.restaurant-detail__name {
    margin-right: 20px;
}

.restaurant-detail__picture {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 50%;
}

.restaurant-detail__info {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}

.info-card {
    flex: 1 1 calc(33.33% - 20px);
    margin-right: 20px;
}

.restaurant-detail__body {
    margin-bottom: 20px;
}

.restaurant-detail__menus {
    margin-bottom: 20px;
}

.menu__category {
    margin-bottom: 10px;
}

.reviews {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.reviews-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
}

.review-card {
    background-color: #fff;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

.review-card__info {
    margin-bottom: 10px;
}

.review-card__info p {
    margin: 0;
}

.review-card__content p {
    margin: 0;
}

@media screen and (max-width: 768px) {
    .info-card {
        flex-basis: calc(50% - 20px);
    }
}

@media screen and (max-width: 480px) {
    .info-card {
        flex-basis: calc(100% - 20px);
    }
}

